*{
    padding: 0;
    margin: 0;
}
.nav{
    border: 2px red solid;
    display: flex;
    justify-content: center;
    justify-content: space-between;
    height: 100px;
    background-color: black;
}
.logo{
    margin: 10px 10px 10px 10px ;
}
.logo img{
    border: 2px red solid;
    width: 168px;
    height: 80px;
    object-fit: cover;
}
.menu{
    margin: auto;
}
.menu ul{
    margin: 10px 0px;
    border: 2px red solid;
    display: flex;
        
}
.menu ul li{
    list-style: none;

}
.menu ul li a{
    text-decoration: none;
    margin: 15px 20px ;
    font-weight: bolder;
    font-size: 20px;
    color: white;
    position: relative;
}
.menu ul li a:hover{
    color: yellow;
    cursor: pointer;  
}
.navMenu::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 3px;
    width: 0%;
    background-color: yellow;
    
}
.navMenu:hover:after{
    width: 100% ;
    transition: all 0.3s;

}
